import item from './goodsList--Item';
import PopUp from '../../../components/my-components/PopUp.vue';
import poster from './posterShare.vue'
import savePic from './savePicToAlbum.js'
export default {
    components: {
        item,
        PopUp,
        poster
    },
    props: {
        goodsList: {
            type: Array,
            default () {
                return [{
                    img: '/static/img/global/tmp.png',
                    goodName: '',
                    color: '',
                    size: '',
                    num: 0, //库存
                    price: '0', //价格
                    saled: 0, //销量
                    status: 0 //0出售中,1已售罄,2仓库中,3回收站
                }]
            }
        },
        toggle: { //隐藏浮层
            type: Boolean,
            default: false
        }
    },
    watch: {
        toggle() {}
    },
    data() {
        return {
            menuItem: {},
            goodData: {},
            staticUrl: '/static/img/global/tmpAct.png',
            show: false,
            show2: false,
            showPoster: false,
            posteInfo: {
                img: '/static/img/temp/poster.jpg',
                type: 'mini', //mini:小程序，h5: H5
            }
        }
    },
    methods: {
        sharePoster(val) {//点击海报模板后触发
            console.log(val);
            let that = this;
            if (val.type == 'share') {
                // uni.showShareMenu({
                //     title: 'ES业务系统小程序'
                // })
            } else if (val.type == 'save') {
                savePic(this, val.info.img); //保存图片至相册
            }
        },
        closeAll() {
            this.show = false;
            this.show2 = false;
        },
        clickItem(val) {
            if (val.type == 'menu-item') {
                // this.show = true;
                if (val.name == '编辑') {
                    this.$emit('click', val);
                } else if (val.name == '下架' || val.name == '上架' || val.name == '恢复' || val.name == '删除') { //需要二次确认
                    this.Dialog.confirm({
                        title: '',
                        message: '您确认' + val.name + '此商品吗？'
                    }).then(() => {
                        this.$emit('click', val)
                    }).catch(() => {
                        // on cancel
                    });
                } else if (val.name == '推广商品') {
                    this.show2 = true;
                }
            } else {
                this.$emit('click', val)
            }
        },  
        clickModel(val1, val2) {
            let that = this;
            if (val1 == '小程序') {
                if (val2 == '微信好友') { //分享事件onShareAppMessage
                } else if (val2 == '二维码海报') {
                    this.posteInfo = {
                        img: '/static/img/temp/poster.jpg',
                        type: 'mini', //mini:小程序，h5: H5
                    };
                    this.showPoster = !this.showPoster;
                }
            } else if (val1 == 'h5') {
                if (val2 == '微信好友') {} else if (val2 == '二维码海报') {
                    this.showPoster = !this.showPoster;
                    this.posteInfo = {
                        img: '/static/img/temp/poster.jpg',
                        type: 'h5', //mini:小程序，h5: H5
                    }
                } else if (val2 == '复制链接') {
                    uni.setClipboardData({
                        data: '这是链接',
                        success: function() {
                            that.Toast('链接已复制')
                        }
                    });
                }
            }  
        }
    }
}
